.App {
  height: 100vh;
  max-width: 100vw;
  font-family: Arial, Helvetica, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
 font-size: 1.4rem;
 color: #e0354b;
 display: flex;
 align-items: center;
 border-bottom: 3px solid #e0354b;
}

.App-header img {
  height: 200px;
  width: 200px;
}

.App-body{
  font-size: 1rem;
  padding: 2em;
  border-bottom: 3px solid #e0354b;
  height: 45%;
}

.App-footer {
  font-size: 1rem;
  padding: 1.2rem;
  text-align: center;
  font-style: italic;
}